import Slider from "react-slick";


import { SlideStlye } from "./style/slidesectionStyle";

const MainSlide = () => {
  const contents = [
    {
      text: " ",
      bold: ""
    },
    {
      text: "  ",
      bold: "50% "
    },
    {
      text: " ",
      bold: "25% "
    },
    {
      text: " ",
      bold: ""
    },
    {
      text: "GolbalSign ",
      bold: "1  "
    },
  ];
  
  const Setting = {
    dots: true,
    infinite: true,
    speed: 1000,
    slideToShow: 1,
    slideToScroll: 1,
    vertical:false,
    arrows:false,
    autoplay:false,
    draggable:false,
    appendDots: dots => (
      <div className="custom_dots">
        <ul>{dots}</ul>
      </div>
  ),
  customPaging: i => (
    <div>
      <span className="dots_txt">{contents[i].text}</span>
      <span className="dots_txt bold">{contents[i].bold}</span>
    </div>
    )
  };
  

  
  return(
      <Slider {...Setting}>
        <div className="items item_1">
          <div className="relative">
            <div className="tit">    </div>
            <div className="sub_tit">   <br/>
      .</div>
          <a href="" className="flex flex_jc_c flex_ai_c"> </a>
          </div>
        </div>
        <div className="items item_2">
          <div className="relative">
            <div className="tit">   50% </div>
            <div className="sub_tit">       .</div>
            <a href="" className="flex flex_jc_c flex_ai_c"> </a>
          </div>
        </div>
        <div className="items item_3">
          <div className="relative">
            <div className="tit">   25% </div>
            <div>    !(net/org/biz/info/pe.kr )</div>
            <a href="" className="flex flex_jc_c flex_ai_c"> </a>
          </div>
        </div>
        <div className="items item_4">
          <div className="relative">
            <div className="tit">  </div>
            <div className="sub_tit">       .</div>
            <a href="" className="flex flex_jc_c flex_ai_c"> </a>
          </div>
        </div>
        <div className="items item_5">
          <div className="relative">
            <div className="tit">GlobalSign  1  </div>
            <div className="sub_tit">SSL    .<br/>
            GlobalSign    1  .</div>
            <a href="" className="flex flex_jc_c flex_ai_c"> </a>
          </div>
        </div>
      </Slider>
  )
}

export default MainSlide;